@import '_reset.scss';
@import '_variables.scss';
@import '_mixins.scss';
@import '_animations.scss';
@import url('https://fonts.googleapis.com/css2?family=Inter&family=Quicksand&family=Roboto+Mono:wght@200;400&display=swap');

.App{
    width: 100%;
    height: 100vh;
    background-color: $gray-color;
    @include flex_setup_col;
  }
  
  #clock{
    background-color: $gray-color;
    width: 100%;
    height: 100vh;
    color:white;
    @include flex_setup_col;
    justify-content: start;
   
   
  }
  
  
  #timer-wrapper{
    width: max-content;
    padding-top:3rem;
    

    #timer-btn-wrapper {
        @include flex_setup_row;
        justify-content: space-between;
        margin: 5vh 1rem;

        .timerBtn {
            width: 5rem;
            height: 5rem;
            border-radius: 50%;
            outline: $gray-color solid 3px;
            color:white;
            cursor: pointer;
            @include flex_setup_col;
            font-size: 1.5rem;
        }

        
    }
  }
  
  #timer-label {
    font-size: 3rem;
    text-align: center;
    
  }
  
  #time-left {
    font-family: 'Roboto Mono', monospace;
    font-size: 9rem;
    font-weight: 100;
    text-align: center;
    margin-bottom: 0.3rem;
    padding: 1rem;
    
    
  }

  .container {
    
    margin-top:10vh;
    @include flex_setup_col;
    justify-content: flex-start;
    width: 100%;
    #settings-title {
      text-align: center;
      width: 100%;
      background-color: black;
      padding: 1rem 2rem;
      cursor: pointer;
      
      #gear {
        justify-self: center;
        align-self: center;
        padding-left: -5px;
      }

      .spinning {
        transform-origin: 48% 50%;
        animation-name: gear-animation;
        animation-duration: 0.5s;
        animation-timing-function: linear;
        animation-iteration-count: infinite;
      }
    }
    
    
  }


  #settings-container {
    max-width: 300px;
    background-color: black;
    border-radius: 2rem;
    

    #settings-title {
      border-radius: 2rem;
      background-color: black;
      transition: all 0.5s ease-in-out;
      
    }

    #settings-wrapper {
      background-color: black;
      border-radius: 0 0 2rem 2rem;
      width: 100%;
      text-align: center;
      background-color: black;
      color:white;
      font-size: 1.35rem;
      overflow: hidden;
      height: 0px;
      transition: all 0.5s ease-in-out;
      .adjust-titles {
        padding-bottom: 2rem;
      }
    }
    
  }

  
  